import Vue from 'vue'
import VueRouter from 'vue-router'
import Add from './add'
import List from './list'

// import 'assets/a.css'
require('assets/a.css')

Vue.use(VueRouter)

// 
// 
// mode: 'history'
const router = new VueRouter({
  routes: [
    {
      path: '/add',
      component: Add
    },
    {
      path: '/list',
      component: List
    }
  ]
})

// 
//  App  #index 
/* eslint-disable no-new */
new Vue({
  router: router
}).$mount('#index')



